// –°–ª—É—á–∞–π–Ω–æ–µ —á–∏—Å–ª–æ –≤ –¥–∏–∞–ø–∞–∑–æ–Ω–µ
export function randomInteger(min, max) {
	let rand = min - 0.5 + Math.random() * (max - min + 1);
	return Math.round(rand);
}


//
//
//
//
// –ü–æ–∑–∏—Ü–∏–æ–Ω–∏—Ä–æ–≤–∞–Ω–∏–µ

// –û—Ç—Å—Ç—É–ø —ç–ª–µ–º–µ–Ω—Ç–∞ –æ—Ç –∫—Ä–∞–µ–≤ —Å—Ç—Ä–∞–Ω–∏—Ü—ã
export function offset(el) {
	var rect = el.getBoundingClientRect(),
		scrollLeft = window.pageXOffset || document.documentElement.scrollLeft,
		scrollTop = window.pageYOffset || document.documentElement.scrollTop;

	return {
		top: rect.top + scrollTop,
		left: rect.left + scrollLeft,
		right: windowWidth - rect.width - (rect.left + scrollLeft),
	}
}

// –°—Ç–æ—Ä–æ–Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—ã
export function getPageSide(item) {
	if (offset(item).left > (windowWidth / 2)) {
		return 'right';
	} else {
		return 'left';
	}
}

// 
// 
// 
// 
// –ú–∞—Å—Å–∏–≤—ã

// –ò–Ω–¥–µ–∫—Å —ç–ª–µ–º–µ–Ω—Ç–∞
export function indexInParent(node) {
	var children = node.parentNode.childNodes;
	var num = 0;
	for (var i = 0; i < children.length; i++) {
		if (children[i] == node) return num;
		if (children[i].nodeType == 1) num++;
	}
	return -1;
}

// –£–Ω–∏–∫–∞–ª–∏–∑–∞—Ü–∏—è –º–∞—Å—Å–∏–≤–∞
export function uniqArray(array) {
	return array.filter(function (item, index, self) {
		return self.indexOf(item) === index;
	});
}

// 
// 
// 
// 
// –ö—É–∫–∏ 

// –£—Å—Ç–∞–Ω–æ–≤–∫–∏ –∫—É–∫–∏
export function setCookie(name, value, hours = 24) {
	const expires = new Date(Date.now() + hours * 60 * 60 * 1000).toUTCString();
	document.cookie = `${name}=${encodeURIComponent(value)}; path=/; expires=${expires}`;
}

// –ü–æ–ª—É—á–µ–Ω–∏–µ –∫—É–∫–∏
export function getCookie(name) {
	const cookies = document.cookie.split('; ').reduce((acc, cookie) => {
		let [key, value] = cookie.split('=');
		acc[key] = decodeURIComponent(value);
		return acc;
	}, {});

	let cookieValue = cookies[name] || null;
	return cookieValue;
}



// 
